html {
    box-sizing: border-box;
  }
  
  *,*::before,
  *::after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
  }

  /**
   * Basic styles for links
   */
  a {
    text-decoration: none;
    color: inherit;
  }
    /**
   * Basic styles for list
   */

  ul{
      list-style-type: none;
  }